import React from "react";
import ProgressBar from "./ProgressBar";
import ModuleCard from "./ModuleCard";

const NivelCard = ({ level }: { level: any }) => {
  return (
    <div className="bg-white shadow-lg rounded-lg p-6">
      <h3 className="text-xl font-bold">{level.name}</h3>
      <p className="text-gray-600">{level.description}</p>
      <ProgressBar progress={level.progress} />

      <div className="mt-4 space-y-2">
        {level.modules.map((module: any) => (
          <ModuleCard key={module.id} module={module} />
        ))}
      </div>

      <button
        className={`mt-4 w-full py-2 ${
          level.examStatus === "locked" ? "bg-gray-400" : "bg-primary-900"
        } text-white rounded`}
        disabled={level.examStatus === "locked"}
      >
        {level.examStatus === "locked" ? "Bloqueado" : "Presentar examen"}
      </button>
    </div>
  );
};

export default NivelCard;
